            <el-date-picker v-model="dayValue" align="right" type="date" placeholder="选择日期" :picker-options="pickerOptions1" format="yyyy-MM-dd">
            </el-date-picker>
          </div>
        </div>
        <div class="operation_item">
          <div class="block">
            <span class="demonstration">产品</span>
            <el-select v-model="valueProduct" placeholder="请选择">
              <el-option v-for="item in optionsProduct" :key="item.product_id" :label="item.show_product_name" :value="item.product_name" selected="selected">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="operation_item">
          <div class="block">
            <span class="demonstration">系统</span>
            <el-select v-model="valueSystem" placeholder="请选择">
              <el-option v-for="item in optionsSystem" :key="item.os_id" :label="item.show_os_name" :value="item.os_name">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="operation_item">
          <el-button type="primary" round @click="searchData">查询</el-button>
        </div>
      </div>
      <!-- chart -->
      <div class="point_box">注：标题下的数为上一时段今日与对比日同时段的变化率</div>
      <el-row style="padding-left:30px;">
        <el-col :span="12" v-for="(item,index) in chartsList" :key="index">
          <div class="charts_box" :id="'charts_box_'+(index+1)"></div>
        </el-col>
      </el-row>
      <!-- 导出数据 -->
      <div class="btn_download">
        <el-button type="primary" round @click="exportData">导出数据</el-button>
      </div>
      <!-- table list -->
      <el-table :data="tableData" stripe style="width: 100%;box-sizing:border-box;padding:24px;" :default-sort="{prop: 'date', order: 'descending'}">
        <el-table-column v-for="item in theadList" :key="item.id" :prop="item.id" :label="item.name" sortable>
          <template slot-scope="scope">
              <span>{{addComma(scope.row[item.id])}}</span>
</template>
        </el-table-column>
      </el-table>
    </main>
  </div>
