<script>
import axios from "axios";
import echarts from "echarts";
import { stringify } from "querystring";
var root = process.env.API_ROOT;
export default {
  name: "App",
  created() {
    let that = this;
    // 请求Select系列
    that.axiosGetSelect();
  },
  mounted() {
    // 获取charts数据
    let that = this;
    // 默认昨天
    that.dayValue = that.fromatDate(Date.now() - 86400000);
    // 由于echart必须在提前生成dom才有效，这里默认给生成图表列表1000个默认
    // 具体生成多少在【echart真正要生成的图表数量】中进行定义
    let defArr = [];
    for (let i = 0; i < 1000; i++) {
      defArr.push({});
    }
    that.chartsList = defArr;
  },
  methods: {
    // 导出数据
    exportData() {
      let that = this;
      axios
        .post(root + "/api/rt_au_nu/save_data", {
          save_data: that.downloadText
        })
        .then(function(response) {
          let status = response.status;
          if ((status >= 200 && status < 300) || status == 304) {
            console.log(response.data);
            window.location.href = root + response.data;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    fromatDate(timestamp) {
      let sDate = new Date(timestamp); //日期
      let sYear = sDate.getFullYear(); //年
      let sMonth = sDate.getMonth() + 1; //月
      let sDay = sDate.getDate(); //日
      let sHour = sDate.getHours(); //小时
      let sMinute = sDate.getMinutes(); //分钟
      let sSecond = sDate.getSeconds(); //秒
      let mSecond = sDate.getMilliseconds(); //毫秒
      return sYear + "-" + this.evenNum(sMonth) + "-" + this.evenNum(sDay);
    },
    evenNum(n) {
      return n < 10 ? "0" + n : "" + n;
    },
    // 请求select系列
    axiosGetSelect() {
      let that = this;
      axios
        .get("./static/allproduct.json")
        .then(function(response) {
          let status = response.status;
          if ((status >= 200 && status < 300) || status == 304) {
            let productData = response.data.data;
            that.optionsProduct = productData;
            // 设置初始产品
            that.valueProduct = productData[0].product_name;
            // 请求系统
            axios
              .get("./static/allosbyproduct.json", {
                product: productData[0]
              })
              .then(function(response) {
                let status = response.status;
                if ((status >= 200 && status < 300) || status == 304) {
                  // 设置初始系统
                  that.valueSystem = response.data.data[0].os_name;
                  that.optionsSystem = response.data.data;
                  // 初始请求数据list
                  let date = that.fromatDate(Date.now() - 86400000);
                  that.axiosPostDataList(date);
                }
              })
              .catch(function(error) {
                console.log(error);
              });
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 查询
    searchData() {
      // 日期
      let date = this.dayValue;
      if (Object.prototype.toString.call(date) === "[object String]") {
        date = date;
      } else {
        date = this.fromatDate(date);
      }
      this.axiosPostDataList(date);
    },
    // 请求数据列表
    axiosPostDataList(date) {
      let that = this;
      // 昨天，即：基准日期
      let ref_date = that.fromatDate(Date.now());
      // 产品列表
      let optionsProduct = that.optionsProduct;
      // 系统列表
      let optionsSystem = that.optionsSystem;
      // 产品
      let product = that.valueProduct;
      // 系统
      let system = that.valueSystem;
      // 启动loading
      that.loading = true;
      axios
        .get("./static/list.json",{
            product: product, //产品
            cur_date: ref_date, //当前日期
            ref_date: date, //参考日期
            granularity: "hour", //查询粒度(hour,minute)
            os: system, //系统
            channel: "", //渠道
            version: "" //版本
          }
        )
        .then(function(response) {
          let status = response.status;
          if ((status >= 200 && status < 300) || status == 304) {
            response.data = response.data.data;
            // 下载数据参数
            let downloadText = response.data.save_data;
            // table thead
            let col_name = response.data.col_name;
            // 今天时间，永远是显示今天时间
            let cur_data = response.data.cur_data;
            // 对比数据，这是选择日期的数据，是变化的
            let ref_data = response.data.ref_data;
            // table显示的是：基础数据+对比数据
            let allArr = cur_data.concat(ref_data);
            // charts数据
            let nameBase = date;
            let nameMain = ref_date;
            let xAxis = [];
            // thead赋值
            that.theadList = col_name;
            // 下载赋值
            that.downloadText = downloadText;
            // table list赋值
            that.tableData = allArr; //table list
            // x轴数据，来源于基础数据，所有时段，hour字段
            // ref_data.forEach(function(item) {
            //   xAxis.push(item.hour);
            // });
            for (let i = 0; i < 24; i++) {
              xAxis.push(that.evenNum(i));
            }
            // 要遍历的图表规则是：去掉日期和时段，即=>根据col_name字段去掉数组0，1
            let chartShowBase = col_name.slice(2);
            // 存储所有表格数据
            let obj = {};
            let objArr = [];
            chartShowBase.forEach(function(pitem, pidx) {
              let seriesBase = [];
              let seriesMain = [];
              // 得到所有基础数据
              ref_data.forEach((citem, cidx) => {
                seriesBase.push(citem[pitem.id]);
              });
              // 得到所有对比数据
              cur_data.forEach((citem, cidx) => {
                seriesMain.push(citem[pitem.id]);
              });
              // 计算增长还是下降，根据对比数据最后一个来进行对比
              let aa = xAxis[cur_data.length - 2];
              // 基础，不变的量
              let base = "";
              // 改变的量
              let change = "";
              if (cur_data.length > 0 && cur_data.length < 2) {
                if (cur_data.length <= 1) {
                  base = ref_data[cur_data.length - 1][chartShowBase[pidx].id];
                  change = cur_data[cur_data.length - 1][chartShowBase[pidx].id];
                }else{
                  base = ref_data[cur_data.length - 2][chartShowBase[pidx].id];
                change = cur_data[cur_data.length - 2][chartShowBase[pidx].id];
                }
              } else {
                base = cur_data[cur_data.length - 2][chartShowBase[pidx].id];
                change = cur_data[cur_data.length - 2][chartShowBase[pidx].id];
              }
              // 计算方式=>(改变量-基础量)/基础量
              let rtn = ((change - base) / base * 100).toFixed(2) + "%";
              let rtnColor = 2; //颜色值，0上，1下，其余数字不变
              if (change - base > 0) {
                rtnColor = 0;
              } else {
                if (change - base < 0) {
                  rtnColor = 1;
                } else {
                  rtnColor = 2;
                }
              }
              obj = {
                el: "charts_box_" + (pidx + 1),
                text: chartShowBase[pidx].name,
                subtext: rtn,
                subColor: rtnColor,
                xAxis: xAxis,
                nameBase: nameBase,
                nameMain: nameMain,
                seriesBase: seriesBase,
                seriesMain: seriesMain
              };
              objArr.push(obj);
            });
            // echart真正要生成的图表数量
            that.chartsList = chartShowBase;
            // 动态生成图表
            objArr.forEach(function(item, idx) {
              that.createEchart(objArr[idx]);
            });
          }
          // 关闭loading
          that.loading = false;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // table排序
    formatter(row, column) {
      return row.address;
    },
    // 用逗号3个隔开数字
    addComma(n) {
      if (Object.prototype.toString.call(n) === "[object Number]") {
        return String(n).replace(/(\d)(?=(\d{3})+$)/g, "$1,");
      } else {
        return n;
      }
    },
    // 动态创建图表
    createEchart(data) {
      let that = this;
      let myChart = echarts.init(document.getElementById(data.el), null, {
        renderer: "svg"
      });
      // 颜色值，0上，1下
      let subColor = data.subColor;
      let subtext = data.subtext;
      let subRtn = "";
      if (subColor == 0) {
        // 上
        subRtn = "#93ce58";
        subtext = subtext + " ⇧";
      } else {
        if (subColor == 1) {
          // 下
          subRtn = "#fa131e";
          subtext = subtext + " ⇩";
        } else {
          // 通用
          subRtn = "#aaa";
          subtext = subtext + " ⇕";
        }
      }
      myChart.setOption({
        title: {
          text: data.text,
          textStyle: {
            fontSize: "18px",
            fontWeight: 400
          },
          subtext: subtext,
          subtextStyle: {
            color: subRtn,
            fontSize: "14px"
          },
          x: "center",
          align: "right"
        },
        color: ["#bfbfbf", "#409EFF"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          },
          formatter: function(params) {
            let rtn = "";
            let rtnArr = [];
            let tit = "";
            params.forEach((item, idx) => {
              let aDataIndex = item.dataIndex;
              let aMarker = item.marker;
              let aSeriesName = item.seriesName;
              let aValue = item.value;
              let aColor = item.color;
              tit = `<div>${aDataIndex}点+${(aDataIndex + 1) % 24}点</div>`;
              rtn = `
                  <div>${aMarker} ${aSeriesName}：${that.addComma(aValue)}</div>
                `;
              rtnArr.unshift(rtn);
            });
            return tit + rtnArr.join("");
          }
        },
        legend: {
          bottom: 0
        },
        dataZoom: [
          {
            type: "inside",
            xAxisIndex: [0],
            start: 1,
            end: 100
          },
          {
            type: "slider",
            show: false,
            xAxisIndex: [0],
            start: 1,
            end: 100
          }
        ],
        xAxis: {
          type: "category",
          data: data.xAxis
        },
        yAxis: {
          axisLabel: {
            formatter: function(val) {
              let rtn = "";
              if (val >= 1000) {
                rtn = val / 1000 + "k";
              } else {
                rtn = val;
              }
              return rtn;
            }
          }
        },
        series: [
          {
            name: data.nameBase,
            type: "line",
            data: data.seriesBase,
            symbol: "circle",
            symbolSize: 6
          },
          {
            name: data.nameMain,
            type: "line",
            data: data.seriesMain,
            areaStyle: {
              normal: {
                color: "rgba(64,158,255,.3)"
              }
            },
            symbol: "circle",
            symbolSize: 6,
            lineStyle: {
              width: 2
            }
          }
        ]
      });
    }
  },
  data() {
    return {
      // loading
      loading: false,
      // 图表数据
      chartsList: [],
      // 日期控件
      pickerOptions1: {
        disabledDate(time) {
          return time.getTime() > Date.now() - 86400000;
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      // 选择日期
      dayValue: "",
      // 产品列表
      optionsProduct: [],
      // 选择产品
      valueProduct: "",
      // 系统列表
      optionsSystem: [],
      // 选择系统
      valueSystem: "",
      // 下载参数
      downloadText: "",
      // table thead
      theadList: [],
      // table list
      tableData: []
    };
  }
};
</script>

<style>
