  z-index: 999;
}
/*1*/
.flip-txt-loading {
  font: 26px Monospace;
  letter-spacing: 5px;
  color: #af3f3f;
  white-space: nowrap;
}
.flip-txt-loading > span {
  -webkit-animation: flip-txt 2s infinite;
  animation: flip-txt 2s infinite;
  display: inline-block;
  -webkit-transform-origin: 50% 50% -10px;
  transform-origin: 50% 50% -10px;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}
.flip-txt-loading > span:nth-child(1) {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
.flip-txt-loading > span:nth-child(2) {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
.flip-txt-loading > span:nth-child(3) {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
.flip-txt-loading > span:nth-child(4) {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}
.flip-txt-loading > span:nth-child(5) {
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
}
.flip-txt-loading > span:nth-child(6) {
  -webkit-animation-delay: 0.6s;
  animation-delay: 0.6s;
}
.flip-txt-loading > span:nth-child(7) {
  -webkit-animation-delay: 0.7s;
  animation-delay: 0.7s;
}
@-webkit-keyframes flip-txt {
  to {
    -webkit-transform: rotateX(1turn);
    transform: rotateX(1turn);
  }
}
@keyframes flip-txt {
  to {
    -webkit-transform: rotateX(1turn);
    transform: rotateX(1turn);
  }
}
/*2*/
.ff_loading_2 {
  margin: -200px 0 0 -200px;
}
.ojlge-box .loading {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  padding: 0.5em;
  vertical-align: middle;
  text-align: center;
  background-color: transparent;
  border: 5px solid transparent;
  border-top-color: #f60;
  border-bottom-color: #f60;
  border-radius: 50% 0;
  -webkit-box-shadow: 0 0 0.25em #f60 inset;
  box-shadow: 0 0 0.25em #f60 inset;
}
.ojlge-box .outer {
  -webkit-animation: ojlge-spin 1.25s infinite linear;
  animation: ojlge-spin 1.25s infinite linear;
}
.ojlge-box .inner {
  -webkit-animation: ojlge-spin 1.35s infinite linear;
  animation: ojlge-spin 1.35s infinite linear;
}
@-webkit-keyframes ojlge-spin {
  0% {
    -webkit-transform: rotateZ(0);
    transform: rotateZ(0);
  }
  100% {
    -webkit-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}
@keyframes ojlge-spin {
  0% {
    -webkit-transform: rotateZ(0);
    transform: rotateZ(0);
  }
  100% {
    -webkit-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}
/*3*/
.ff_loading_3 {
  margin: -200px 0 0 0;
}
.spinner {
  width: 50px;
  height: 50px;
  background-color: #561bb9;
  -webkit-animation: rotateplane 1.5s infinite ease-in-out;
  animation: rotateplane 1.5s infinite ease-in-out;
}
@-webkit-keyframes rotateplane {
  0% {
    -webkit-transform: perspective(120px);
  }
  50% {
    -webkit-transform: perspective(120px) rotateY(180deg);
  }
  100% {
    -webkit-transform: perspective(120px) rotateY(180deg) rotateX(180deg);
  }
}
@keyframes rotateplane {
  0% {
    transform: perspective(120px) rotateX(0) rotateY(0);
    -webkit-transform: perspective(120px) rotateX(0) rotateY(0);
  }
  50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0);
    -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0);
  }
  100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}
/*4*/
.ff_loading_4 {
  margin: -200px 0 0 200px;
}
.line-scale-pulse-out {
  -webkit-transform: scale(1);
  transform: scale(1);
}
.line-scale-pulse-out > div {
  background-color: #11d4c5;
  width: 4px;
  height: 35px;
  border-radius: 2px;
  margin: 2px;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  display: inline-block;
  -webkit-animation: line-scale-pulse-out 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
  animation: line-scale-pulse-out 0.9s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
}
.line-scale-pulse-out > div:nth-child(2),
.line-scale-pulse-out > div:nth-child(4) {
  -webkit-animation-delay: -0.4s !important;
  animation-delay: -0.4s !important;
}
.line-scale-pulse-out > div:nth-child(1),
.line-scale-pulse-out > div:nth-child(5) {
  -webkit-animation-delay: -0.2s !important;
  animation-delay: -0.2s !important;
}
@-webkit-keyframes line-scale-pulse-out {
  0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
}
@keyframes line-scale-pulse-out {
  0% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
  50% {
    -webkit-transform: scaley(0.4);
    transform: scaley(0.4);
  }
  100% {
    -webkit-transform: scaley(1);
    transform: scaley(1);
  }
}
/*5*/
.ff_loading_5 {
  margin: 200px 0 0 0;
}
.pacman {
  position: relative;
  -webkit-transform: scale(1);
  transform: scale(1);
}
.pacman > div:nth-child(2) {
  -webkit-animation: pacman-balls 1s -0.99s infinite linear;
  animation: pacman-balls 1s -0.99s infinite linear;
}
.pacman > div:nth-child(3) {
  -webkit-animation: pacman-balls 1s -0.66s infinite linear;
  animation: pacman-balls 1s -0.66s infinite linear;
}
.pacman > div:nth-child(4) {
  -webkit-animation: pacman-balls 1s -0.33s infinite linear;
  animation: pacman-balls 1s -0.33s infinite linear;
}
.pacman > div:nth-child(5) {
  -webkit-animation: pacman-balls 1s 0s infinite linear;
  animation: pacman-balls 1s 0s infinite linear;
}
.pacman > div:first-of-type {
  width: 0;
  height: 0;
  border-right: 25px solid transparent;
  border-top: 25px solid #c2d411;
  border-left: 25px solid #c2d411;
  border-bottom: 25px solid #c2d411;
  border-radius: 25px;
  -webkit-animation: rotate_pacman_half_up 0.5s 0s infinite;
  animation: rotate_pacman_half_up 0.5s 0s infinite;
  position: relative;
  left: -30px;
}
.pacman > div:nth-child(2) {
  width: 0;
  height: 0;
  border-right: 25px solid transparent;
  border-top: 25px solid #c2d411;
  border-left: 25px solid #c2d411;
  border-bottom: 25px solid #c2d411;
  border-radius: 25px;
  -webkit-animation: rotate_pacman_half_down 0.5s 0s infinite;
  animation: rotate_pacman_half_down 0.5s 0s infinite;
  margin-top: -50px;
  position: relative;
  left: -30px;
}
.pacman > div:nth-child(3),
.pacman > div:nth-child(4),
.pacman > div:nth-child(5),
.pacman > div:nth-child(6) {
  background-color: #c2d411;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  margin: 2px;
  width: 10px;
  height: 10px;
  position: absolute;
  -webkit-transform: translate(0, -6.25px);
  transform: translate(0, -6.25px);
  top: 25px;
  left: 70px;
}
@-webkit-keyframes rotate_pacman_half_up {
  0% {
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
  }
}
@keyframes rotate_pacman_half_up {
  0% {
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(270deg);
    transform: rotate(270deg);
  }
}
@-webkit-keyframes rotate_pacman_half_down {
  0% {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
  }
  50% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
  }
}
@keyframes rotate_pacman_half_down {
  0% {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
  }
  50% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
  }
}
@-webkit-keyframes pacman-balls {
  75% {
    opacity: 0.7;
  }
  100% {
    -webkit-transform: translate(-100px, -6.25px);
    transform: translate(-100px, -6.25px);
  }
}
@keyframes pacman-balls {
  75% {
    opacity: 0.7;
  }
  100% {
    -webkit-transform: translate(-100px, -6.25px);
    transform: translate(-100px, -6.25px);
  }
}
/*6*/
.ff_loading_6 {
  margin: 200px 0 0 -200px;
}
.ball-scale-multiple {
  position: relative;
  -webkit-transform: scale(1) translateY(-30px);
  transform: scale(1) translateY(-30px);
}
.ball-scale-multiple > div:nth-child(2) {
  -webkit-animation-delay: -0.4s;
  animation-delay: -0.4s;
}
.ball-scale-multiple > div:nth-child(3) {
  -webkit-animation-delay: -0.2s;
  animation-delay: -0.2s;
}
.ball-scale-multiple > div {
  background-color: #ff3cb2;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  margin: 2px;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  position: absolute;
  left: -30px;
  top: 0;
  opacity: 0;
  margin: 0;
  width: 60px;
  height: 60px;
  -webkit-animation: ball-scale-multiple 1s 0s linear infinite;
  animation: ball-scale-multiple 1s 0s linear infinite;
}
@-webkit-keyframes ball-scale-multiple {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}
@keyframes ball-scale-multiple {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0;
  }
}
/*7*/
.ff_loading_7 {
  margin: 200px 0 0 200px;
}
.ball-clip-rotate-pulse {
  position: relative;
  -webkit-transform: scale(1) translateY(-15px);
  transform: scale(1) translateY(-15px);
}
.ball-clip-rotate-pulse > div {
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 100%;
}
.ball-clip-rotate-pulse > div:first-child {
  background: #0bc18f;
  height: 16px;
  width: 16px;
  top: 7px;
  left: -7px;
  -webkit-animation: scale 1s 0s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
  animation: scale 1s 0s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
}
.ball-clip-rotate-pulse > div:last-child {
  position: absolute;
  border: 2px solid #fff;
  width: 30px;
  height: 30px;
  left: -16px;
  top: -2px;
  background: 0 0;
  border: 2px solid;
  border-color: #0cd89f transparent #0cd89f transparent;
  -webkit-animation: rotate 1s 0s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
  animation: rotate 1s 0s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
}
@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);
  }
  50% {
    -webkit-transform: rotate(180deg) scale(0.6);
    transform: rotate(180deg) scale(0.6);
  }
  100% {
    -webkit-transform: rotate(360deg) scale(1);
    transform: rotate(360deg) scale(1);
  }
}
@keyframes rotate {
  0% {
    -webkit-transform: rotate(0) scale(1);
    transform: rotate(0) scale(1);
  }
  50% {
    -webkit-transform: rotate(180deg) scale(0.6);
    transform: rotate(180deg) scale(0.6);
  }
  100% {
    -webkit-transform: rotate(360deg) scale(1);
    transform: rotate(360deg) scale(1);
  }
}
@-webkit-keyframes scale {
  30% {
    -webkit-transform: scale(0.3);
    transform: scale(0.3);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes scale {
  30% {
    -webkit-transform: scale(0.3);
    transform: scale(0.3);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
/*8*/
.ff_loading_8 {
  margin: 0 0 0 -200px;
}
.roataqx-loader {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  -webkit-perspective: 800px;
  perspective: 800px;
  position: relative;
}
.roataqx-loader > div {
  position: absolute;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.roataqx-loader .one {
  left: 0;
  top: 0;
  -webkit-animation: rotate-one 1s linear infinite;
  animation: rotate-one 1s linear infinite;
  border-bottom: 3px solid #000;
}
.roataqx-loader .two {
  right: 0;
  top: 0;
  -webkit-animation: rotate-two 1s linear infinite;
  animation: rotate-two 1s linear infinite;
  border-right: 3px solid #000;
}
.roataqx-loader .three {
  right: 0;
  bottom: 0;
  -webkit-animation: rotate-three 1s linear infinite;
  animation: rotate-three 1s linear infinite;
  border-top: 3px solid #000;
}
@-webkit-keyframes rotate-one {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0);
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}
@keyframes rotate-one {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(0);
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}
@-webkit-keyframes rotate-two {
  0% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(0);
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0);
  }
  100% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}
@keyframes rotate-two {
  0% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(0);
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0);
  }
  100% {
    -webkit-transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}
@-webkit-keyframes rotate-three {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(0);
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}
@keyframes rotate-three {
  0% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(0);
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0);
  }
  100% {
    -webkit-transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}
/*9*/
.ff_loading_9 {
  margin: 0 0 0 200px;
}
.multi-ball {
  -webkit-animation-duration: 10s;
  animation-duration: 10s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-name: multi-ball-fullrotate;
  animation-name: multi-ball-fullrotate;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
}
.multi-ball > div {
  position: absolute;
  background-color: #333;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  border-radius: 50%;
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-name: multi-ball-loader;
  animation-name: multi-ball-loader;
  -webkit-animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.multi-ball > div:nth-child(1) {
  width: 40px;
  height: 40px;
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
.l.multi-ball > div:nth-child(2) {
  width: 60px;
  height: 60px;
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s;
}
.multi-ball > div:nth-child(3) {
  width: 80px;
  height: 80px;
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
.multi-ball > div:nth-child(4) {
  width: 100px;
  height: 100px;
  -webkit-animation-delay: -1.5s;
  animation-delay: -1.5s;
}
.multi-ball > div:nth-child(5) {
  width: 120px;
  height: 120px;
  -webkit-animation-delay: -2s;
  animation-delay: -2s;
}
.multi-ball > div:nth-child(6) {
  width: 140px;
  height: 140px;
  -webkit-animation-delay: -2.5s;
  animation-delay: -2.5s;
}
.multi-ball > div:nth-child(7) {
  width: 160px;
  height: 160px;
  -webkit-animation-delay: -3s;
  animation-delay: -3s;
}
@-webkit-keyframes multi-ball-loader {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(0);
    transform: translateX(-50%) translateY(-50%) rotate(0);
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 0;
    background-color: #333;
    -webkit-transform: translateX(-50%) translateY(-50%) rotateX(180deg)
      rotate(360deg);
    transform: translateX(-50%) translateY(-50%) rotateX(180deg) rotate(360deg);
  }
}
@keyframes multi-ball-loader {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(0);
    transform: translateX(-50%) translateY(-50%) rotate(0);
  }
  50% {
    opacity: 0.7;
  }
  100% {
    opacity: 0;
    background-color: #333;
    -webkit-transform: translateX(-50%) translateY(-50%) rotateX(180deg)
      rotate(360deg);
    transform: translateX(-50%) translateY(-50%) rotateX(180deg) rotate(360deg);
  }
}
@-webkit-keyframes multi-ball-fullrotate {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
@keyframes multi-ball-fullrotate {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
  }
}
</style>



